import detailInfo from '../../components/detail/detail-info.vue'
import time from '../../common/time.js'
import commentList from '../../components/detail/comment-list.vue'
import userChatBottom from '../../components/user-chat/user-chat-bottom.vue'
import moreShare from '../../components/common/more-share.vue'
export default {
	components:{
		detailInfo,
		commentList,
		userChatBottom,
		moreShare
	},
	data() {
		return {
			shareshow:false,
			comment:{
				count:20,
				list:[
				]		
			},
			detail:{
				userpic:'../../static/图片素材/userpic/12.jpg',
				username:'昵称',
				sex:0,//0代表男，1代表女
				age:25,
				isguanzhu:false,
				title:'我是标题',
				titlepic:'../../static/图片素材/datapic/11.jpg',
				morepic:['../../static/图片素材/datapic/11.jpg','../../static/图片素材/datapic/12.jpg',
				'../../static/图片素材/datapic/13.jpg','../../static/图片素材/datapic/14.jpg'],
				share:false,
				video:false,
				path:"深圳 龙岗",
				commentnum:10,
				sharenum:10,
				goodnum:55
			}
		}
	},
	methods: {
		// 初始化对象
		initdata(obj){
			uni.setNavigationBarTitle({
				// 修改窗口标题
			    title: obj.title
			});
		},
		// 获取评论
		getcomment(){
			let arr = [
					{
						// 一级评论
					id:1,
					fid:0,
					userpic:'../../static/图片素材/banner2.jpg',
					username:'小猫咪',
					time:'1554970014',
					data:'支持股从昂贵'
					},
					{
						id:2,
						fid:1,
						userpic:'../../static/图片素材/banner2.jpg',
						username:'小猫咪',
						time:'1554970014',
						data:'支持股从昂贵'
					},
					{
						id:3,
						fid:1,
						userpic:'../../static/图片素材/banner2.jpg',
						username:'小猫咪',
						time:'1554970014',
						data:'支持股从昂贵'
					},
					{
					id:4,
					fid:0,
					userpic:'../../static/图片素材/banner2.jpg',
					username:'小猫咪',
					time:'1554970014',
					data:'支持股从昂贵'
					}
			]
			for(let i =0 ; i<arr.length;i++){
			arr[i].time=time.gettime.gettime(arr[i].time)
			}
			this.comment.list = arr
		},
		submit(data){
			let obj = {
				id:1,
				fid:0,
				userpic:'../../static/图片素材/banner2.jpg',
				username:'小猫咪',
				time:time.gettime.gettime(new Date().getTime()),
				data:data
			}
			this.comment.list.push(obj)
			console.log(data)
		},
		// 显示分享
		showShare(){
			this.shareshow = !this.shareshow
		}
	},
	onLoad(e){
		// console.log(e.detailData)
		// 把字符串转化成对象
		this.initdata(JSON.parse(e.detailData))
		this.getcomment()
	},
	// 监听导航按钮
	onNavigationBarButtonTap(){
		if(e.index == 0){
		this.showShare()
		}
	}
}
